:root {
  --accent: #3b82f6;
  --text-primary: #1f2937;
  --text-secondary: #6b7280;
  --text-muted: #9ca3af;
  --border-light: #e5e7eb;
  --border-focus: #3b82f6;
  --bg-white: #ffffff;
  --bg-gray-50: #f9fafb;
  --bg-gray-100: #f3f4f6;
  --shadow-md: 0 4px 6px rgba(0, 0, 0, 0.10);
  --radius-md: 6px;
  --radius-lg: 12px;
}
body {
  background: var(--bg-gray-100);
  color: var(--text-primary);
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, sans-serif;
}
h1, h2, h4 {
  font-family: inherit;
}
.App {
  text-align: center;
}
