.list-container {
  max-width: 1000px;
  margin: 2rem auto;
  background: var(--bg-white, #fff);
  border-radius: 12px;
  box-shadow: 0 4px 6px rgba(0,0,0,0.05);
  padding: 2rem;
  min-height: 400px;
}
.list-container h2 {
  margin-bottom: 1.5rem;
  color: var(--text-primary, #1f2937);
}
.expenses-table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 1rem;
  font-size: 1rem;
}
.expenses-table th, .expenses-table td {
  padding: 0.65rem 0.5rem;
  border-bottom: 1px solid var(--border-light, #e5e7eb);
  text-align: left;
}
.expenses-table th {
  background: var(--bg-gray-100, #f3f4f6);
  font-weight: 600;
}
.expenses-table td {
  background: var(--bg-white, #fff);
}
.list-actions {
  display: flex;
  align-items: center;
  gap: 1rem;
  margin-bottom: 1rem;
}
.error {
  color: #dc2626;
  margin: 1.5rem;
}
.empty-state {
  text-align: center;
  color: var(--text-muted, #9ca3af);
  font-style: italic;
  padding: 2rem;
}
